@import '_normalize';
@import '_mixins';

* {
	margin: 0;
	padding: 0;
	outline: 0;
	min-height: 0;
	min-width: 0;
	box-sizing: border-box;
	scroll-behavior: smooth;
}

g {
	width: 3rem;
}

body {
	min-height: 100vh;
	font-size: 100%;
	position: relative;
	font-weight: 400;
	color: #d1cdc7;
	font-family: 'Roboto Mono', monospace;
	scroll-behavior: smooth;
}

:root {
	--articleItem-bc: #ffffff1a;
	--app-bc: #090b0b;
	--article-bc: #060b19;
}

a {
	color: #d1cdc7;
	text-decoration: none;
}
h1 {
	font-size: 4rem;
}

button {
	background-color: transparent;
	border: 0;
	cursor: pointer;
	padding: 0;
}

li {
	list-style: none;
}

.icon-style {
	color: #753ceb;
}

.container {
	max-width: 56vw;
	margin: 0 auto;
	padding: 0 15px;
}

.app {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	background-color: var(--app-bc);

	// &:after {
	// // 	background: #682ae9
	// // 	linear-gradient(180deg, #0e1212, rgba(14, 18, 18, 0) 20vh);
	// // 	content: '';
	// // 	display: block;
